import React from 'react'
import { useContext } from 'react'
import { Context } from '../../context'
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/navigation";
import { Navigation } from "swiper";

import "./Staff.scss"

export default function App() {

  const {staff} = useContext(Context)

  console.log(staff)
  return (
    <>
      <Swiper navigation={true} modules={[Navigation]} className="mySwiper">
          {staff.map( el =>{
            
            return ( 
              <SwiperSlide>
                <div className="main--staff--div  ">
                  <div>
                    <img className="main--staff--div--img" src="" alt="" />
                </div>
                <div className="">
                  <div className="d-flex">
                    <h2>{el.name}</h2>
                    <h2>{el.surname}</h2>
                  </div>
                  <div>
                    <div className="d-flex">
                      <h5 className="main--staff--div--title"> <span className="staff-span">Образование:</span> {el.education}</h5>
                    </div>
                    <div>
                      <p className="main--staff--div--sub--title">
                      {el.info}
                      {el.project}
                      </p>
                    </div>
                    <div className="d-flex">
                      <h5><span className="staff-span">Опыт работы:</span> </h5>
                      <h5>{el.experience}</h5>
                    </div>
                </div>
                </div>
              </div>
            </SwiperSlide>
           )
        } )}
      </Swiper>
    </>
  );
}
